@import "../../scss/variable";
@import "../../scss/mixins";

.marque-container {
  height: 5vh;
  padding-top: 1rem;
  width: 20%;
  margin: auto;
  @include respond(tab-port) {
    height: 0vh;
    width: 60%;
    padding-bottom: 2rem;
  }
  &-tag {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    & > span {
      color: $light-blue;
      margin-left: 2rem;
      font-weight: bold;
      font-size: 1.5rem;
    }
    & > p {
      font-weight: 300;
      font-size: 1.3rem;
      color: white;
      margin-left: 2rem;
    }
  }
}
